import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import {
  onPageInsurance,
  setStateInsurance,
  onPageMore,
  setStateMore,
  onPageBusiness,
  setStateBusiness,
} from '../../actions/navActions';


// Footer
import FooterContainer from '../../containers/FooterContainer';

import { hideNavFooterIfMobile } from '../../lib/helperFunctions';

class Disclaimer extends Component {
  constructor(props) {
    super(props);
    // reset indication
    const resetInsurance = onPageInsurance.NONE;
    const resetMore = onPageMore.NONE;
    const resetBusiness = onPageBusiness.NONE;
    const {
      resetIndicatorInsurance,
      resetIndicatorMore,
      resetIndicatorBusiness,
    } = this.props;
    resetIndicatorInsurance(resetInsurance);
    resetIndicatorMore(resetMore);
    resetIndicatorBusiness(resetBusiness);
  }

  componentDidMount() {
    hideNavFooterIfMobile(this.props.location);
  }

  render() {
    window.scrollTo(0, 0);
    return (
      <div className="fds">
        <div className="title-container">
          <div className="title">Disclaimer</div>
        </div>
        <div className="content">
          <div className="content-row">
            <div className="title-desc">
              *Disclaimer: PolicyPal does not, at any time, underwrite, originate, or issue insurance policies and/or insurance contracts. The underwriting, origination, and issuance of the insurance policies and/or insurance contracts are the sole provision and responsibility of the respective insurance company that a User may contract with through the PolicyPal app. Thus, any policies, liabilities, coverage, or responsibilities relating to an insurance policy purchased through PolicyPal (directly or indirectly) remain the sole province of the insurance company that provided that policy/contract. Furthermore, any claims or disputes in relation to policy/contract claims should be addressed to the underwriting, originating, or issuing insurance company as specified in that policy/contract. PolicyPal is unable to mediate, assist, convey, or dispute any claims or other issues related to the liability, coverage, or scope of any insurance product.
            </div>
            <div className="title-desc">
              Please also note that PolicyPal will not be liable for any mistake, error, or omission that may occur during the electronic processing of the policies/contracts on the platform. PolicyPal will use its best efforts to ensure that any mistake, error, or omission is corrected when discovered. Users should inform PolicyPal in the event of their discovery of any mistake, error, omission, and/or update in the policies/contracts or their user details.
            </div>
          </div>
        </div>
        <FooterContainer />
      </div>
    );
  }
}

Disclaimer.defaultProps = {
  resetIndicatorInsurance: null,
  resetIndicatorMore: null,
  resetIndicatorBusiness: null,
};
Disclaimer.propTypes = {
  resetIndicatorInsurance: PropTypes.func,
  resetIndicatorMore: PropTypes.func,
  resetIndicatorBusiness: PropTypes.func,
  location: PropTypes.object.isRequired,
};

const mapDispatchToProps = dispatch => ({
  resetIndicatorInsurance: (resetInsurance) => {
    dispatch(setStateInsurance(resetInsurance));
  },
  resetIndicatorMore: (resetMore) => {
    dispatch(setStateMore(resetMore));
  },
  resetIndicatorBusiness: (resetBusiness) => {
    dispatch(setStateBusiness(resetBusiness));
  },
});

export default connect(null, mapDispatchToProps)(Disclaimer);

